.card-content {
    display: flex; 
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.game-card {
    max-width: calc(65vh - 65px);
    z-index: 1000;
}
.game-card button{
    margin-top: 10px;
    background: #fff !important;
}
.game-card-media {
    margin-top: 10px;
}
.actions {
    padding: 1px;
    width: 100%;
    margin: 1px;
    display:flex;
    justify-content:space-between; 
    border-top: #000 1px solid;
}
.confetti {
    z-index: 5000 !important;
    top: 40px !important;
}
.trophy {
    z-index: 1000;
}